.hero {
  position: relative;
  display: flex;
  justify-content: center;
  grid-area: hero;
  width: 100%;
  height: 100%;

  &__background {
    position: absolute;
    top: 0;
    z-index: -100;
  }

  &__container {
    align-items: center;

    @include xs {
      grid-template-columns: 1fr;
      gap: 0;
    }
  }

  &__hero__btn {
    align-self: flex-start;
  }

  &__right {
    grid-column: span 6;
    margin-left: 14%;

    @include xl {
      margin-left: 20px;
    }

    @include lg {
      display: none;
    }
  }

  &__left {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    grid-column: span 6;
    margin-top: -40px;
    padding: 30px 0;
    height: 100%;

    @include lg {
      grid-column: span 12;
      margin-left: 30px;
    }

    @include md {
      margin: 0 auto;
      width: auto;
    }
  }

  &__wrapper-img {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -3;
    width: 100%;
  }

  &__img {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -2;
    display: block;
    width: 100%;
    background-color: #000;
    object-fit: cover;
  }

  &__backround {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-color: var(--color-mine-alpha85);
  }

  &__title {
    margin: 0;
    margin-bottom: var(--main-offset);
    max-width: 510px;
    font: var(--middle-font-xxl);
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: var(--color-white);
  }

  &__descr {
    margin: 0;
    margin-bottom: var(--main-offset);
    max-width: 560px;
    font: var(--small-regular);
    color: var(--color-white);
  }

  .swiper-button-next::after,
  .swiper-button-prev::after {
    content: "";
  }

  &__btn {
    align-self: flex-start;
  }

  .swiper-button-next,
  .swiper-button-prev {
    display: flex;
    width: auto;
    height: auto;
    user-select: none;

    svg {
      display: inline-block;
      width: 40px;
      height: 16px;
      fill: var(--color-white);
    }

    span {
      display: inline-block;
      font: var(--middle-font-md);
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: var(--color-white);
    }
  }

  .swiper-button-prev {
    position: absolute;
    left: 0;
    top: initial;
    bottom: 0;

    span {
      margin-left: 25px;
    }
  }

  .swiper-button-next {
    position: absolute;
    right: 0;
    top: initial;
    bottom: 0;

    span {
      margin-right: 25px;
    }
  }

  .hero-container {
    position: relative;
  }
}

.hero-container {
  display: flex;
  justify-content: flex-start;
  width: 350px;
  height: 570px;
}
