.header {
  grid-area: header;
  padding: 51px 0;

  &__container {
    display: grid;
    grid-template: auto / 1fr 150px;
    padding: 0 15px;

    @include xl {
      grid-template: 1fr 2fr / minmax(auto, 1fr);
    }

    @include lg {
      grid-template: auto 120px / 1fr;
    }

    @include md {
      justify-items: space-between;
      grid-auto-flow: column;
      grid-template: auto / 50px 1fr;
    }
  }

  .burger {
    display: none;

    @include md {
      @include burger;

      display: inline-block;
    }
  }

  &__contact {
    align-self: center;
    justify-self: center;

    @include xl {
      align-self: center;
      justify-self: center;
    }

    @include md {
      justify-self: end;
    }
  }

  &__contact-link {
    position: relative;
    align-items: center;
    width: 100%;
    font: var(--middle-font-sm);
    color: var(--color-white);

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: -8px;
      width: 100%;
      height: 2px;
      background-color: var(--color-gold);
    }
  }

  @include xl {
    margin: 0;
  }
}
