.footer {
  padding: 75px 0;

  &__wrapper {
    @include xs {
      grid-template-columns: 1fr;
      gap: 0;
    }
  }

  &__left {
    grid-column: span 4;

    @include lg {
      order: 1;
      grid-column: span 12;
    }
  }

  &__right {
    grid-column: span 8;

    @include lg {
      order: 0;
      grid-column: span 12;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    font: var(--middle-font-sm);
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: var(--color-white);

    @include lg {
      flex-flow: row wrap;
      justify-content: space-between;
      text-align: right;
    }

    @include sm {
      font: 700 10px / normal var(--font-family);
    }
  }

  &__item {
    display: inline-block;

    &:not(:last-child) {
      margin-bottom: 25px;
    }

    div {
      &:not(:last-child) {
        margin-bottom: 25px;
      }
    }
  }

  @include md {
    padding: 45px 0;
  }
}
