.about-us {
  padding: 150px 0 75px;

  &__container {
    &:not(:last-child) {
      margin-right: var(--second-offset);
    }
  }

  &__wrapper {
    justify-items: end;

    @include sm {
      justify-items: center;
    }

    @include xs {
      grid-template-columns: 1fr;
      gap: 0;
    }
  }

  &__blocks-icon {
    @include sm {
      display: flex;
    }
  }

  &__block-icon {
    display: inline-block;
    margin-bottom: 25px;

    @include sm {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-bottom: 0;

      &:not(:last-child) {
        margin-right: 60px;
      }
    }

    @include xs {
      margin-bottom: 0;
      width: calc((100% - 30px) / 2);

      &:not(:last-child) {
        margin-right: 30px;
      }
    }
  }

  &__wrapper-icon {
    margin-bottom: 20px;
    width: 70px;
    height: 70px;
  }

  svg {
    width: 100%;
    height: 100%;
    fill: var(--color-white);
    object-fit: cover;
  }

  &__text {
    grid-column: span 5;

    @include lg {
      grid-column: span 8;
    }

    @include sm {
      grid-column: span 12;
      padding: 10px;
      text-align: center;
    }

    @include xs {
      grid-column: 1;
    }
  }

  &__subtitle {
    display: inline-block;
    margin: 0;
    margin-bottom: var(--third-offset);
    font: var(--middle-font-xl);
    text-transform: uppercase;
    color: var(--color-white);
  }

  &__descr {
    margin: 0;
    font: var(--small-regular);
    color: var(--color-white);
  }

  &__number {
    display: inline-block;
    grid-column: span 3;

    @include lg {
      grid-column: span 4;
    }

    @include sm {
      display: block;
      grid-column: span 12;
      padding: 10px;
      text-align: center;
    }
  }

  &__short-text {
    display: block;
    font: var(--middle-font-sm);
    text-align: left;
    color: var(--color-white);

    div {
      display: inline-block;
      color: var(--color-gold);
    }

    @include sm {
      text-align: center;

      div {
        display: block;
      }
    }
  }

  &__img {
    grid-column: span 4;
    margin-top: auto;
    width: 100%;
    height: 100%;
    animation: effectImg 1s infinite;

    @include lg {
      display: none;
    }
  }

  @include md {
    padding: 90px 0 45px;
  }
}
