.rating {
  padding: 75px 0;

  &__container {
    @include lg {
      margin: 0 auto;
      padding: 0;
    }
  }

  &__wrapper {
    position: relative;
    left: 0;
    top: 0;
    z-index: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 15px;
    height: auto;
    min-height: 550px;
    background-image: url("../img/rating.jpg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-blend-mode: hue;
    animation: opacityBackgroundRating 10s linear infinite;

    @keyframes opacityBackgroundRating {
      0% {
        background-color: #3330;
      }

      50% {
        background-color: var(--color-mine-alpha85);
      }

      100% {
        background-color: #3330;
      }
    }
  }

  &__years {
    display: inline-flex;
    flex-direction: column;
    margin-top: -20px;
    margin-right: 100px;
    width: auto;

    @include md {
      margin-right: 0;
    }

    @include sm {
      margin-right: -10px;
    }
  }

  &__year {
    display: block;
    width: 100%;
    font: var(--high-bold);
    letter-spacing: 0.1em;
    color: var(--color-gold);

    @include md {
      font: 700 50px / normal var(--font-family);
    }
  }

  &__title {
    margin: 0;
    max-width: 470px;
    font: var(--high-bold-xxl);
    color: var(--color-white);

    @include lg {
      font: 700 30px / 150% var(--font-family);
    }

    @include sm {
      font: 700 21px / 150% var(--font-family);
    }
  }

  &__stars {
    display: inline-flex;
    justify-content: flex-start;
    margin-bottom: 25px;
    height: 24px;
  }

  &__star {
    position: relative;
    margin-bottom: 25px;
    width: 26px;
    height: inherit;
    animation: brightnessStar 10s linear infinite;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: -100;
      width: 1px;
      height: 1px;
      box-shadow: 0 0 25px 15px rgba(255, 255, 255, 1);
      background-color: none;
      transform: translate(-50%, -50%);
      animation: opacityStar 10s linear infinite;

      @keyframes opacityStar {
        0% {
          opacity: 0;
        }

        50% {
          opacity: 1;
        }

        100% {
          opacity: 0;
        }
      }
    }

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &:not(:last-child) {
      margin-right: 25px;
    }

    @include sm {
      width: 22px;

      &:not(:last-child) {
        margin-right: 15px;
      }
    }

    @keyframes brightnessStar {
      0% {
        filter: brightness(150%) blur(1px);
      }

      50% {
        filter: brightness(100%);
      }

      100% {
        filter: brightness(150%) blur(1px);
      }
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    margin-left: 20px;
    padding: 0 15px 15px;
    height: 100%;
  }

  @include md {
    padding: 45px 0;
  }
}
