.form {
  &__top {
    display: flex;
    justify-content: flex-end;
    margin-bottom: var(--third-offset);
    width: auto;

    @include sm {
      flex-direction: column;
      min-width: 320px;
    }

    @include xs {
      min-width: 270px;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;

    @include sm {
      flex-flow: column-reverse wrap;
      align-items: center;
      margin-bottom: 30px;
      min-width: 320px;

      > *:nth-child(n):last-child {
        margin-bottom: 30px;
      }
    }
  }

  &__inputs {
    display: inline-flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-right: var(--second-offset);
    min-width: 340px;

    @include lg {
      min-width: auto;
    }

    @include sm {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__input {
    flex-shrink: 0;
    border: 1px solid var(--dove-gray);
    padding: 15px;
    width: calc((100% - var(--second-offset)) / 2);
    height: 35px;
    max-width: 100%;
    font: var(--small-semibold);
    color: var(--dusty-gray);
    background-color: var(--color-tundora);

    &:nth-child(n+1):nth-child(-n+2) {
      margin-bottom: 25px;
    }

    @include xs {
      width: calc((100% - 10px) / 2);
    }
  }

  &__textarea {
    width: 310px;
    height: 100px;
    color: var(--dusty-gray);
    background-color: var(--color-tundora);
    resize: none;

    @include lg {
      width: 100%;
      min-width: 150px;
      max-width: 270px;
    }

    @include sm {
      min-width: 100%;
      max-width: 100%;
    }
  }

  &__info {
    max-width: 390px;
    font: var(--small-regular-xxs);
    color: var(--dusty-gray);

    @include sm {
      text-align: center;
    }
  }
}
