.perfomance {
  padding: 75px 0;
  width: auto;

  &__title {
    margin-right: auto;
    margin-left: auto;
    text-align: center;
  }

  &__wrapper {
    @include lg {
      grid-template-columns: minmax(auto, 800px);
      overflow: hidden;
      margin-right: 110px;
      margin-left: 150px;
    }

    @include md {
      margin: 0 60px;
    }

    @include sm {
      margin: 0 30px;
    }

    @include xs {
      grid-template-columns: minmax(1140px, auto);
      overflow: initial;
      margin: 0;
    }
  }

  &__container {
    margin: 0 auto;
  }

  &__btn-search {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 100;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 100px;
    height: 100px;
    background-color: var(--color-gold);
    opacity: 0;
    visibility: 0;
    transform: translate(-50%, -50%);
    cursor: pointer;

    svg {
      width: 30px;
      height: 30px;
      object-fit: cover;
    }
  }

  &__img {
    position: relative;
    width: inherit;
    height: inherit;
    background-color: #000;
    transform-style: preserve-3d;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 10;
      width: inherit;
      height: inherit;
      background: var(--color-mine-alpha65);
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &:hover {
      &::after {
        background: none;
      }
    }
  }

  .perfomance-container {
    justify-self: start;
    margin: 0;
  }

  .swiper-wrapper {
    height: 550px;
  }

  .swiper-slide {
    width: 455px;
    height: inherit;

    &:hover {
      .perfomance__img {
        transform: rotateY(180deg) scale(-1, 1);
        transition-property: transform ease-in;
        transition-duration: 1s;
        transition-delay: 1s;

        &::after {
          background: transparent;
        }
      }

      .perfomance__btn-search {
        opacity: 1;
        visibility: 1;
        transition: opacity 0.3s, ease 1s;
      }
    }

    @include md {
      padding: 45px 0;
    }

    @include sm {
      width: 375px;
    }
  }
}
