.nav {
  &__list {
    display: inline-flex;
    align-items: center;
    text-transform: uppercase;

    @include md {
      flex-direction: column;
      align-items: start;
    }
  }

  &__item {
    position: relative;
    display: inline-flex;

    &:not(:last-child) {
      margin-right: 22px;
    }

    @include md {
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 35px;
      }
    }
  }

  &__link {
    font: var(--middle-font);
    letter-spacing: 0.1em;
    color: var(--color-white);

    &:hover {
      color: var(--color-gold);
      filter: drop-shadow(0 0 5px var(--color-gold));
      transition: 1s 0.5s;
      transition-property: color, filter;

      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -5px;
        width: 100%;
        height: 2px;
        background-color: var(--color-gold);
        animation: hoverLink 1s;

        @keyframes hoverLink {
          0% {
            width: 0;
          }

          50% {
            width: 100%;
          }
        }
      }
    }

    @include lg {
      font: 700 14px / normal var(--font-family);
    }

    @include md {
      font: 700 24px / normal var(--font-family);
      letter-spacing: 0.05em;
    }
  }

  &__close {
    position: absolute;
    right: 38px;
    top: 38px;
    display: none;
    border: none;
    padding: 0;
    width: 22px;
    height: 22px;
    color: var(--color-white);
    background-color: transparent;
    cursor: pointer;

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      border-radius: 5px;
      width: 100%;
      height: 2px;
      background-color: currentColor;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }

    @include md {
      display: inline-block;
    }
  }

  @include lg {
    margin-left: 30px;
  }

  @include md {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 102;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 0;
    padding: 38px;
    padding-top: 140px;
    width: 360px;
    min-height: 100vh;
    background-color: var(--color-mine);
    transform: translateX(-150vw);
    transition: 0.3s;
  }

  @include xl {
    align-self: center;
    justify-self: center;
  }

  @include xs {
    width: auto;
  }
}

.open {
  transform: translateX(0);
  transition: 0.3s;
}
