.side-panel {
  position: relative;
  display: grid;
  align-items: flex-end;
  grid-area: side-panel;
  padding: 86px 0;
  width: 150px;
  min-height: 100%;
  background-color: var(--color-mine-alpha75);

  &__text {
    position: absolute;
    left: 50%;
    top: 40%;
    font: var(--middle-font-md);
    letter-spacing: 0.1em;
    text-transform: uppercase;
    writing-mode: tb-rl;
    color: var(--color-silver);
    transform: rotate(180deg) translate(50%, 50%);
    user-select: none;

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      top: -121px;
      width: 3px;
      height: 80px;
      background-color: var(--color-silver);
      transform: translateX(-50%);
    }
  }

  @include md {
    display: none;
  }
}

.social {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  &__item {
    display: inline-block;

    &:not(:last-child) {
      margin-bottom: var(--second-offset);
    }
  }

  &__link {
    display: inline-block;

    svg {
      width: 100%;
      height: 100%;
      fill: var(--color-white);
      fill-opacity: 0.6;
      object-fit: cover;
    }

    &:hover {
      svg {
        fill: var(--color-gold);
        fill-opacity: 1;
      }
    }

    &--insta {
      width: 26px;
      height: 26px;
    }

    &--facebook {
      width: 15px;
      height: 26px;
    }

    &--vk {
      width: 26px;
      height: 14px;
    }

    &--youtube {
      width: 25px;
      height: 18px;
    }
  }
}
