.partners {
  padding: 75px 0;

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
  }

  &__item {
    margin-right: 80px;
    margin-bottom: 80px;
    width: calc((100% - 240px) / 4);

    &:nth-child(4n) {
      margin-right: 0;

      @include lg {
        margin-right: 40px;
      }
    }

    @include lg {
      margin-right: 40px;
      margin-bottom: 40px;
      width: calc((100% - 120px) / 3);

      &:nth-child(3n) {
        margin-right: 0;
      }
    }

    @include md {
      width: calc((100% - 80px) / 3);

      &:nth-child(3n) {
        margin-right: 0;
      }
    }

    @include sm {
      width: calc((100% - 40px) / 2);

      &:nth-child(3n) {
        margin-right: 40px;
      }

      &:nth-child(2n) {
        margin-right: 0;
      }
    }

    @include xs {
      margin-right: 0;
      width: 100%;

      &:nth-child(3n) {
        margin-right: 0;
      }
    }
  }

  &__link {
    display: inline-block;
    width: 200px;
    height: 96px;
    min-width: 100%;
    max-width: 100px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  @include md {
    padding: 45px 0;
  }
}

.swiper-partners {
  @include xs {
    display: flex;
    width: 100%;
  }

  .swiper-wrapper {

    @include xs {

    }
  }
}
